@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,700);
@charset "UTF-8";
#board {
  display: flex;
  flex-wrap: wrap;
  height: 300px;
  overflow: hidden;
  margin: 50px auto 30px auto;
  position: relative;
  width: 300px; }
  @media (min-width: 450px) {
    #board {
      height: 450px;
      width: 450px; } }
  #board .square {
    background-color: #d1ccc0;
    border-radius: 10px;
    cursor: pointer;
    color: #fff;
    height: 90px;
    margin: 5px;
    width: 90px;
    position: relative;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s; }
    @media (min-width: 450px) {
      #board .square {
        height: 140px;
        width: 140px; } }
  #board.player-1 .square:hover {
    background-color: #474787; }
  #board.player-2 .square:hover {
    background-color: #cc8e35; }
  #board .square.player-1, #board .square.selected.player-1 {
    background-color: #706fd3; }
  #board .square.player-2, #board .square.selected.player-2 {
    background-color: #ffb142; }
  #board .square:hover:after {
    opacity: 0.4; }
  #board .square:after {
    left: 0;
    font-family: "FontAwesome";
    font-size: 45px;
    margin-top: -22.5px;
    opacity: 0;
    position: absolute;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    top: 50%;
    width: 100%; }
    @media (min-width: 450px) {
      #board .square:after {
        font-size: 60px;
        margin-top: -30px; } }
  #board.player-1 .square:after,
  #board .square.selected.player-1:after {
    content: ""; }
  #board.player-2 .square:after,
  #board .square.selected.player-2:after {
    content: ""; }
  #board .square.selected.player-1:after, #board .square.selected.player-2:after {
    opacity: 1; }

#status {
  background: rgba(255, 255, 255, 0.8);
  bottom: 5px;
  color: #333;
  display: none;
  left: 5px;
  padding-top: 55px;
  position: absolute;
  right: 5px;
  top: 5px;
  text-align: center;
  z-index: 11; }
  @media (min-width: 450px) {
    #status {
      padding-top: 110px; } }
  #status.visible {
    display: block; }
  #status h3 {
    font-size: 30px;
    font-weight: 300; }
    @media (min-width: 450px) {
      #status h3 {
        font-size: 40px; } }
  #status a {
    background-color: #333;
    border-radius: 4px;
    color: #fff;
    padding: 14px 45px;
    text-decoration: none;
    -webkit-transition: background-color 0.2s;
    transition: background-color 0.2s; }
    #status a:hover {
      background-color: #333;
      cursor: pointer; }

body {
  color: #333;
  font-family: "Arial", sans-serif;
  margin: 0;
  text-align: center; }

#game {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

